(function () {
  'use strict';

  angular
    .module('trcWorks', [
      'ngResource'
    ]);

})();

(function () {
  'use strict';

  angular
    .module('trcWorks')
    .provider('worksRepo', worksRepoProvider);

  /**
   * This functionality is already being implemented as a standalone trc-biblio module. The code is
   * used here as an interim until the Bower build/dependency workflow has been finalized.
   *
   * @return {angular.provider}
   */
  function worksRepoProvider() {
    var provider = {};
    provider.url = '/api/works';
    provider.$get = worksRepoFactory;
    return provider;

    /** @ngInject */
    function worksRepoFactory($resource, _) {
      var restResource = $resource(provider.url + '/:workId', { workId: '@id' }, {
        update: {
          method: 'PUT'
        }
      });

      var editionResource = $resource(provider.url + '/:workId/editions/:editionId', { editionId: '@id' }, {
        update: {
          method: 'PUT'
        }
      });

      var volumeResource = $resource(provider.url + '/:workId/editions/:editionId/volumes/:volumeId', { volumeId: '@id' }, {
        update: {
          method: 'PUT'
        }
      });

      var repo = {};
      repo.getAll = findAll;
      repo.search = search;
      repo.searchByAuthor = searchByAuthor;
      repo.get = getWork;
      repo.getWork = getWork;
      repo.getEdition = getEdition;
      repo.getVolume = getVolume;
      repo.create = createWork;
      repo.createWork = createWork;
      repo.createEdition = createEdition;
      repo.createVolume = createVolume;
      repo.save = save;
      repo.saveWork = saveWork;
      repo.saveEdition = saveEdition;
      repo.saveVolume = saveVolume;
      repo.delete = deleteWork;
      repo.getTitle = getTitleByTypePreference;
      return repo;

      /**
       * Retrieves a listing of all works
       *
       * @return {WorkProxy[]}
       */
      function findAll() {
        return search('');
      }

      /**
       * Search for works by query
       *
       * @return {SearchResultSet}
       */
      function search(query) {
        return restResource.get({ q: query, max: 50 });
      }

      /**
       * Search for works by author
       *
       * @return {SearchResultSet}
       */
      function searchByAuthor(authorId) {
        return restResource.get({ aid: authorId, max: 50 });
      }

      /**
       * Retrieves a work object by identifier.
       *
       * A proxy object will be returned immediately, and its properties will be populated after the
       * request succeds. If fine-grained asynchronous handling, etc. is required, the request's
       * promis is available on Work.$promise.
       *
       * @param {string} workId
       * @return {Work}
       */
      function getWork(workId) {
        if (angular.isUndefined(workId)) {
          throw new TypeError('Invalid ID provided to getWork');
        }

        var work = restResource.get({ workId: workId });

        work.$promise.then(adaptWork);

        return work;
      }

      /**
       * Retrieves an edition object by identifier.
       *
       * A proxy object will be returned immediately, and its properties will be populated after the
       * request succeds. If fine-grained asynchronous handling, etc. is required, the request's
       * promis is available on Edition.$promise.
       *
       * @param {string} workId
       * @param {string} editionId
       * @return {Edition}
       */
      function getEdition(workId, editionId) {
        if (angular.isUndefined(workId) || angular.isUndefined(editionId)) {
          throw new TypeError('Invalid IDs provided to getEdition');
        }

        var edition = editionResource.get({ workId: workId, editionId: editionId });

        edition.$promise.then(adaptEdition);

        return edition;
      }

      /**
       * Retrieves a volume object by identifier.
       *
       * A proxy object will be returned immediately, and its properties will be populated after the
       * request succeds. If fine-grained asynchronous handling, etc. is required, the request's
       * promis is available on Volume.$promise.
       *
       * @param {string} workId
       * @param {string} editionId
       * @param {string} volumeId
       * @return {Volume}
       */
      function getVolume(workId, editionId, volumeId) {
        if (angular.isUndefined(workId) || angular.isUndefined(editionId) || angular.isUndefined(volumeId)) {
          throw new TypeError('Invalid IDs provided to getVolume');
        }

        var volume = volumeResource.get({ workId: workId, editionId: editionId, volumeId: volumeId });

        volume.$promise.then(adaptVolume);

        return volume;
      }

      /**
       * Creates a new work instance
       *
       * @return {Work}
       */
      function createWork() {
        var work = new restResource();
        adaptWork(work);
        work.titles.push({});
        work.authors.push({});
        return work;
      }

      /**
       * Creates a new edition instance
       *
       * @return {Edition}
       */
      function createEdition() {
        var edition = new editionResource();
        return adaptEdition(edition);
      }

      /**
       * Creates a new volume instance
       *
       * @return {Volume}
       */
      function createVolume() {
        var volume = new volumeResource();
        return adaptVolume(volume);
      }

      /**
       * Dispatch method for saving works, editions, and volumes
       *
       * @deprecated use saveWork, saveEdition, or saveVolume directly
       * @return {Promise}
       */
      function save() {
        switch (arguments.length) {
          case 1: return saveWork.apply(null, arguments);
          case 2: return saveEdition.apply(null, arguments);
          case 3: return saveVolume.apply(null, arguments);
        }
        throw new TypeError('Unexpected arguments to worksRepo#save');
      }

      /**
       * Saves a work instance back to the server
       *
       * @param {Work} work
       * @return {Promise.<Work>}
       */
      function saveWork(work) {
        if (!(work instanceof restResource)) {
          throw new Error('work was not created by this repo');
        }

        // ngResource automatically updates the model to reflect any server changes (e.g. setting IDs for the work/edtions/volumes/copies).
        return work.id
          ? work.$update()
          : work.$save();
      }

      /**
       * Saves an edition instance back to the server
       *
       * @param  {string} workId
       * @param  {Edition} edition
       * @return {Promise.<Edition>}
       */
      function saveEdition(workId, edition) {
        if (!workId) {
          throw new Error('no work id provided');
        }

        if (!(edition instanceof editionResource)) {
          throw new Error('edition was not created by this repo');
        }

        return edition.id
          ? edition.$update({ workId: workId })
          : edition.$save({ workId: workId });
      }

      /**
       * Saves a volume instance back to the server
       *
       * @param  {string} workId
       * @param  {string} editionId
       * @param  {Volume} volume
       * @return {Promise.<Volume>}
       */
      function saveVolume(workId, editionId, volume) {
        if (!workId) {
          throw new Error('no work id provided');
        }

        if (!editionId) {
          throw new Error('no edition id provided');
        }

        if (!(volume instanceof volumeResource)) {
          throw new Error('volume was not created by this repo');
        }

        return volume.id
          ? volume.$update({ workId: workId, editionId: editionId })
          : volume.$save({ workId: workId, editionId: editionId });
      }

      /**
       * Deletes a work instance from the server
       *
       * @param  {string} id
       */
      function deleteWork(workId) {
        var dataItem = restResource.delete({ id: workId });
        return dataItem.$promise;
      }

      /**
       * Ensure that aggregate fields common to all work/edition/volume entities exist and are formatted correctly
       *
       * @param  {Work|Edition|Volume} entity
       * @return {Work|Edition|Volume}
       */
      function adaptCommon(entity) {
        if (!entity.titles) {
          entity.titles = [];
        } else {
          entity.titles.forEach(function (title) {
            title.type = title.type.toLowerCase();
          });
        }

        if (!entity.authors) {
          entity.authors = [];
        } else {
          entity.authors.forEach(function (author) {
            author.role = author.role.toLowerCase();
          });
        }

        if (!entity.copies) {
          entity.copies = [];
        }

        return entity;
      }

      /**
       * Ensure that work aggregate fields exist and are formatted correctly
       *
       * @param  {Work} work
       * @return {Work}
       */
      function adaptWork(work) {
        adaptCommon(work);

        if (!work.editions) {
          work.editions = [];
        } else {
          work.editions.forEach(adaptEdition);
        }

        return work;
      }

      /**
       * Ensure that edition aggregate fields exist and are formatted correctly
       *
       * @param  {Edition} edition
       * @return {Edition}
       */
      function adaptEdition(edition) {
        adaptCommon(edition);

        if (!edition.volumes) {
          edition.volumes = [];
        } else {
          edition.volumes.forEach(adaptVolume);
        }

        return edition;
      }

      /**
       * Ensure that volume aggregate fields exist and are formatted correctly
       *
       * @param  {Volume} volume
       * @return {Volume}
       */
      function adaptVolume(volume) {
        adaptCommon(volume);

        return volume;
      }

      /**
       * Retrieves the first available title of the given type or falsey if no title can be found.
       * If multiple types are given, returns the title corresponding to the first type for which a title is found.
       *
       * @param  {Title[]}         titles
       * @param  {string|string[]} types
       * @return {Title}
       */
      function getTitleByTypePreference(titles, types) {
        if (angular.isUndefined(types)) {
          types = ['short', 'canonical', 'bibliographic'];
        } else if (!angular.isArray(types)) {
          types = [types];
        }

        return types.reduce(function (found, type) {
          return found || _.find(titles, { type: type });
        }, null);
      }
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('trcTasks', [
      'ngResource'
    ]);

})();

/**
 * @typedef {object} Workflow
 * @property {string} id
 * @property {string} label
 * @property {string} description
 * @property {object.<string, WorkflowStage>} stages
 * @property {string} status
 */

/**
 * @typedef {object} WorkflowStage
 * @property {string} id
 * @property {string} label
 * @property {string} description
 * @property {boolean} isFinal
 * @property {WorkflowStageTransition[]} transitions
 */

/**
 * @typedef {object} WorkflowStageTransition
 * @property {string} sourceStage
 * @property {string} targetStage
 * @property {string} label
 */

/**
 * @typedef {object} Worklist
 * @property {string} groupId
 * @property {string} label
 * @property {integer} itemCount
 * @property {integer} start
 * @property {integer} max
 * @property {WorkItem[]} items
 */

/**
 * @typedef {object} WorkItem
 * @property {string} itemId
 * @property {string} type
 * @property {string} entityId
 * @property {string} label
 * @property {object.<string,string>} properties
 * @property {string} stage
 * @property {string} task
 */

(function () {
  'use strict';

  angular
    .module('trcTasks')
    .provider('tasksRepo', tasksRepoProvider);

  /**
   * @return {angular.provider}
   */
  function tasksRepoProvider() {
    var provider = {};
    provider.url = '/api/tasks';
    provider.$get = tasksRepoFactory;
    return provider;

    /** @ngInject */
    function tasksRepoFactory($resource) {
      var taskResource = $resource(provider.url + '/:taskId', { taskId: '@id' });
      var workflowResource = $resource(provider.url + '/:taskId/workflow');
      var itemResource = $resource(provider.url + '/:taskId/items/:itemId', { itemId: '@itemId' });

      var repo = {};
      repo.getTask = getTask;
      repo.getWorkflow = getWorkflow;
      repo.all = getAllTasks;
      repo.getItems = getItems;
      repo.transition = transition;
      return repo;

      /**
       * Retrieves a listing of all tasks
       *
       * @return {Task[]}
       */
      function getAllTasks() {
        return taskResource.query();
      }

      /**
       * Retrieves a task by id
       *
       * @param  {string} taskId
       * @return {GroupedTaskList}
       */
      function getTask(taskId) {
        return taskResource.get({ taskId: taskId });
      }

      /**
       * Retrieves a workflow by task id
       * @param  {string} taskId
       * @return {Workflow}
       */
      function getWorkflow(taskId) {
        return workflowResource.get({ taskId: taskId });
      }

      /**
       * Gets items from the task endpoint for a given stage
       *
       * @param  {string}  [taskId]  Task ID
       * @param  {string}  stageId          Workflow stage for which to get items
       * @param  {object}  options
       * @param  {integer} [options.start]  Starting index
       * @param  {integer} [options.max]    Maximum number of items to fetch per page
       * @return {Worklist}
       */
      function getItems(taskId, stageId, options) {
        options = options || {};
        return itemResource.get({
          taskId: taskId,
          stage: stageId,
          start: options.start,
          max: options.max
        });
      }

      /**
       * Transition an item from its current stage to a target stage
       *
       * @param  {WorkItem}                item   Work item instance
       * @param  {WorkflowStageTransition} transition Target workflow stage
       * @return {WorkItem}                       Updated work item instance
       */
      function transition(taskId, item, transition) {
        if (item.stage !== transition.sourceStage) {
          throw new Error('Transition (' + transition.sourceStage + ' -> ' + transition.targetStage + ') is not valid for item currently in stage ' + item.stage);
        }

        var result = itemResource.save({
          taskId: taskId,
          itemId: item.itemId
        }, {
          stage: transition.targetStage,
          // NOTE comments are not saved yet
          comments: null
        });

        // propagate updates to the original item
        result.$promise.then(function (resp) {
          angular.extend(item, resp);
        });

        return result;
      }
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('trcReln', [
      'ngResource'
    ]);

})();

// NOTE: this entire API model *WILL* eventually change

/**
 * @typedef Relationship
 * @type {object}
 * @property {string}     id                  - A unique identifier for this relationship
 * @property {string}     typeId              - The ID of the RelationshipType of this relationship
 * @property {string}     description         - A detailed description of this relationship
 * @property {string}     descriptionMimeType - MIME type string of the description
 * @property {Provenance} provenance          - Metadata about the relationship record
 * @property {Anchor[]}   relatedEntities     - A set of "source" anchors
 * @property {Anchor[]}   targetEntities      - A set of "target" anchors
 */

/**
 * @typedef RelationshipType
 * @type {object}
 * @property {string}  identifier   - A unique identifier for this type
 * @property {string}  title        - A label to display for this type
 * @property {string}  reverseTitle - If this type is directed, the label to display for reverse relationships
 * @property {string}  description  - A more detailed description of this type
 * @property {boolean} isDirected   - Whether this type is directed (true) or undirected (false)
 */

/**
 * @typedef Provenance
 * @type {object}
 * @property {string[]} creatorUris  - URIs to people/authors of this relationship
 * @property {string}   dateCreated  - an ISO 8601 date representation of when the record was created
 * @property {string}   dateModified - an ISO 8601 date representation of when the record was last modified
 */

/**
 * @typedef Anchor
 * @type {object}
 * @property {string[]} entryUris[] - URIs to work/edition/volume
 */

/**
 * @typedef TypeGroup
 * @type {object}
 * @property {string}                   label         - Potentially directed display label for group
 * @property {RelationshipType}         type          - The type corresponding to this group
 * @property {boolean}                  reverse       - For directed types, pecifies the directionality of relationships in this group
 * @property {NormalizedRelationship[]} relationships - Relationships of the same type and direction
 */

/**
 * @typedef NormalizedRelationship
 * @type {object}
 * @property {string}                           id          - The id of the original relationship
 * @property {string}                           typeId      - The relationship's type id
 * @property {boolean}                          reverse     - Whether this relationship is in the reverse type direction
 * @property {string}                           description - A description of this relationship
 * @property {Entity[]}                         entities    - A set of target entities referenced by this relationship
 * @property {Promise.<NormalizedRelationship>} $promise    - Resolves to the normalized promise when all asynchronous data has been set
 */

/**
 * @typedef Entity
 * @type {object}
 * @property {string}              label               - A display label for the entity
 * @property {string}              type                - One of 'work', 'edition', or 'volume'
 * @property {object}              refParams           - Reference parameters for resolving the entity
 * @property {string}              refParams.workId    - The entity's work id
 * @property {string}              refParams.editionId - If an edition or volume, the entity's edition id
 * @property {string}              refParams.volumeId  - If a volume, the entity's volume id
 * @property {Work|Edition|Volume} entity              - The entity itself
 * @property {Promise.<Entity>}    $promise            - Resolves to the entity when all asynchronous data has been set
 */

(function () {
  'use strict';

  var ENTITY_URI = /^works\/([^\/]+)(?:\/editions\/([^\/]+)(?:\/volumes\/([^\/]+))?)?$/i;

  angular
    .module('trcReln')
    .provider('relationshipsRepo', relationshipsRepoProvider);

  function relationshipsRepoProvider() {
    var provider = {};
    provider.url = '/api/relationships';
    provider.$get = relationshipsRepoFactory;
    return provider;

    /** @ngInject */
    function relationshipsRepoFactory($q, $resource) {
      var typeResource = $resource(provider.url + '/types/:id', { id: '@identifier' });
      var relnResource = $resource(provider.url + '/:id', { id: '@id' }, {
        update: { method: 'PUT' }
      });

      var typesCache = null;

      var repo = {};
      repo.getTypes = getTypes;
      repo.getType = getType;
      repo.createRelationship = createRelationship;
      repo.createProvenance = createProvenance;
      repo.createAnchor = createAnchor;
      repo.get = findById;
      repo.search = search;
      repo.save = saveRelationship;
      repo.delete = deleteRelationship;
      repo.normalizeRelationships = normalizeRelationships;
      return repo;

      /**
       * Fetches relationship types from the server, caching them in the process.
       * The returned array will be populated when the request resolves.
       * The underlying promise is available on the $promise property.
       *
       * @param  {object} options
       * @param  {boolean} options.force Discard cache (if set) and refetch
       * @return {object.<string,RelationshipType>}
       */
      function getTypes(options) {
        options = options || {};

        if (!typesCache || options.force) {
          var types = typeResource.query();

          typesCache = {};

          var tcPromise = types.$promise.then(function () {
            types.forEach(function (type) {
              typesCache[type.identifier] = type;
            });
            return typesCache;
          });

          // add $promise as a non-enumerable property
          Object.defineProperty(typesCache, '$promise', {
            value: tcPromise
          });
        }

        return typesCache;
      }

      /**
       * Fetches a type by ID
       *
       * @param  {string} id
       * @return {Promise.<RelationshipType>}
       */
      function getType(id) {
        var types = getTypes();

        return types.$promise.then(function (types) {
          return types[id];
        });
      }

      /**
       * Creates a new empty relationship instance
       *
       * @return {Relationship}
       */
      function createRelationship() {
        var reln = new relnResource();
        return adaptRelationship(reln);
      }

      /**
       * Creates a new empty provenance model
       *
       * @return {Provenance}
       */
      function createProvenance() {
        var provenance = {};
        return adaptProvenance(provenance);
      }

      /**
       * Creates a new empty anchor model
       *
       * @return {Anchor}
       */
      function createAnchor() {
        var anchor = {};
        return adaptAnchor(anchor);
      }

      /**
       * Retrieves a relationship from the server by ID.
       * The returned model will be populated once the request succeeds.
       * The underlying promise can be accessed via the $promise property.
       *
       * @param  {string} id
       * @return {Relationship}
       */
      function findById(id) {
        return relnResource.get({ id: id });
      }

      /**
       * Find all relationships matching the given criteria.
       * @param  {object|string} options
       * @property {string} options.uri (default if options is a string)
       * @property {string} options.typeId
       * @property {string} options.direction "from" (out-relationships), "to" (in-relationships), or "any"
       * @property {integer} options.start
       * @property {integer} options.max
       * @return {Relationship[]}
       */
      function search(options) {
        if (!options) {
          throw new Error('no options provided')
        }

        if (angular.isString(options)) {
          // treat single string as an entity URL
          options = { uri: options };
        }

        var queryOpts = {};

        if (options.uri) {
          queryOpts.entity = options.uri;
        }

        if (options.typeId) {
          queryOpts.type = options.typeId;
        }

        if (options.direction) {
          queryOpts.direction = options.direction;
        }

        if (options.start) {
          queryOpts.off = options.start;
        }

        if (options.max) {
          queryOpts.max = options.max;
        }

        return relnResource.query(queryOpts);
      }

      /**
       * Saves a relationship back to the server.
       *
       * @param  {Relationship} reln
       * @return {Promise} resolves on successful save
       */
      function saveRelationship(reln) {
        if (!(reln instanceof relnResource)) {
          throw new Error('Relationship was not created by this repo');
        }

        return reln.id
          ? reln.$update()
          : reln.$save();
      }

      /**
       * Deletes a relationship from the server via the REST API.
       *
       * @param  {string} id
       * @return {Promise} resolves on success
       */
      function deleteRelationship(id) {
        var response = relnResource.delete({ id: id });
        return response.$promise
      }

      /**
       * Ensures aggregate fields on a relationship are set appropriately
       *
       * @param  {Relationship} reln
       * @return {Relationship}
       */
      function adaptRelationship(reln) {
        if (!reln.provenance) {
          reln.provenance = createProvenance();
        }

        if (!reln.relatedEntities) {
          reln.relatedEntities = [];
        }

        if (!reln.targetEntities) {
          reln.targetEntities = [];
        }

        return reln;
      }

      /**
       * Ensures aggregate fields on a provenance model are set appropriately
       *
       * @param  {Provenance} provenance
       * @return {Provenance}
       */
      function adaptProvenance(provenance) {
        if (!provenance.creatorUris) {
          provenance.creatorUris = [];
        }

        return provenance;
      }

      /**
       * Ensures aggregate fields on an anchor model are set appropriately
       *
       * @param  {Anchor} anchor
       * @return {Anchor}
       */
      function adaptAnchor(anchor) {
        if (!anchor.entryUris) {
          anchor.entryUris = [];
        }

        return anchor;
      }

      /**
       * Normalizes relationships into type groups that are more suitable for display in the UI
       *
       * @param  {Relationship[]} relationships
       * @return {TypeGroup[]}
       */
      function normalizeRelationships(relationships, currentUri, worksRepo) {
        var normRelns = relationships.map(function (relationship) {
          var isReverse = relationship.targetEntities.some(function (anchor) {
            return anchor.entryUris.some(function (uri) {
              return uri === currentUri;
            });
          });

          var targetField = isReverse ? 'relatedEntities' : 'targetEntities';

          var entities = flatMap(relationship[targetField], property('entryUris'))
            .map(function (uri) {
              return resolveEntityUri(uri, worksRepo);
            });

          var normReln = {
            id: relationship.id,
            typeId: relationship.typeId,
            reverse: isReverse,
            description: relationship.description,
            entities: entities
          };

          var entityPs = entities.map(property('$promise'));

          Object.defineProperty(normReln, '$promise', {
            value: $q.all(entityPs).then(constant(normReln))
          });

          return normReln;
        });

        var relnsByTypeDir = {};

        normRelns.forEach(function (relationship) {
          var typeId = relationship.typeId;

          if (!relnsByTypeDir.hasOwnProperty(typeId)) {
            relnsByTypeDir[typeId] = {
              fwd: [],
              rev: []
            };
          }

          relnsByTypeDir[typeId][relationship.reverse ? 'rev' : 'fwd'].push(relationship);
        });

        var typeGroups = [];

        var typeGroupPs = kvMap(relnsByTypeDir, function (dirs, typeId) {
          return getType(typeId).then(function (type) {
            if (type.isDirected) {
              // add forward group
              if (dirs.fwd.length > 0) {
                typeGroups.push({
                  label: type.title,
                  type: type,
                  reverse: false,
                  relationships: dirs.fwd
                });
              }

              // add reverse group
              if (dirs.rev.length > 0) {
                typeGroups.push({
                  label: type.reverseTitle,
                  type: type,
                  reverse: true,
                  relationships: dirs.rev
                });
              }
            } else {
              // combine forward/reverse and add as a single group
              var group = dirs.fwd.concat(dirs.rev);
              if (group.length > 0) {
                typeGroups.push({
                  label: type.title,
                  type: type,
                  relationships: group
                });
              }
            }
          });
        });

        Object.defineProperty(typeGroups, '$promise', {
          value: $q.all(typeGroupPs).then(constant(typeGroups))
        });

        return typeGroups;
      }
    }
  }

  /**
   * Resolves an entity URI into a uniform container for the referenced entity.
   * @param  {string} uri
   * @param  {WorksRepository} worksRepo
   * @return {Entity}
   */
  function resolveEntityUri(uri, worksRepo) {
    var match = ENTITY_URI.exec(uri);

    if (!match) {
      return null;
    }

    if (match[1] && match[2] && match[3]) {
      return makeVolumeEntity(match[1], match[2], match[3], worksRepo);
    } else if (match[1] && match[2]) {
      return makeEditionEntity(match[1], match[2], worksRepo);
    } else if (match[1]) {
      return makeWorkEntity(match[1], worksRepo);
    }

    return null;
  }

  /**
   * Creates an entity for a volume.
   * @param  {string} workId
   * @param  {string} editionId
   * @param  {string} volumeId
   * @param  {WorksRepository} worksRepo
   * @return {Entity}
   */
  function makeVolumeEntity(workId, editionId, volumeId, worksRepo) {
    var edition = worksRepo.getEdition(workId, editionId);
    var volume = worksRepo.getVolume(workId, editionId, volumeId);

    var entity = {
      type: 'volume',
      refParams: {
        workId: workId,
        editionId: editionId,
        volumeId: volumeId
      },
      entity: volume
    };

    var labelP = volume.$promise.then(function () {
      var title = worksRepo.getTitle(volume.titles, ['short', 'canonical', 'bibliographic']);
      var label = formatTitle(title);

      return edition.$promise.then(function () {
        return label + '. ' + edition.editionName + '. Volume ' + volume.volumeNumber;
      }, function () {
        return label + '. [Unknown edition]. Volume ' + volume.volumeNumber;
      });
    });

    var entityP = labelP.then(function (label) {
      entity.label = label;
      return entity;
    });

    Object.defineProperty(entity, '$promise', {
      value: entityP
    });

    return entity;
  }

  /**
   * Creates an entity for an edition.
   * @param  {string} workId
   * @param  {string} editionId
   * @param  {WorksRepository} worksRepo
   * @return {Entity}
   */
  function makeEditionEntity(workId, editionId, worksRepo) {
    var edition = worksRepo.getEdition(workId, editionId);

    var entity = {
      type: 'edition',
      refParams: {
        workId: workId,
        editionId: editionId
      },
      entity: edition
    };

    var entityP = edition.$promise.then(function () {
      var title = worksRepo.getTitle(edition.titles, ['short', 'canonical', 'bibliographic']);
      entity.label = formatTitle(title) + '. ' + edition.editionName;
      return entity;
    });

    Object.defineProperty(entity, '$promise', {
      value: entityP
    });

    return entity;
  }

  /**
   * Creates an entity for a work.
   * @param  {string} workId
   * @param  {WorksRepository} worksRepo
   * @return {Entity}
   */
  function makeWorkEntity(workId, worksRepo) {
    var work = worksRepo.getWork(workId);

    var entity = {
      type: 'work',
      refParams: {
        workId: workId
      },
      entity: work
    };

    var entityP = work.$promise.then(function () {
      var title = worksRepo.getTitle(work.titles, ['short', 'canonical', 'bibliographic']);
      entity.label = formatTitle(title);
      return entity;
    });

    Object.defineProperty(entity, '$promise', {
      value: entityP
    });

    return entity;
  }

  /**
   * Formats a bibliographic entity's title into a title string
   * @param  {Title} title
   * @return {string}
   */
  function formatTitle(title) {
    if (!title) {
      return '';
    }

    return title.title + (title.subtitle ? ': ' + title.subtitle : '');
  }

  // UTILITY FUNCTIONS

  /**
   * @param {object} o
   * @return {function}
   */
  function constant(o) {
    return function () {
      return o;
    };
  }

  /**
   * Maps over an object's key/value pairs
   * @param {object.<string, object>} obj
   * @param {function} cb
   * @return {array}
   */
  function kvMap(obj, cb) {
    return Object.keys(obj)
      .filter(function (k) {
        return obj.hasOwnProperty(k);
      })
      .map(function (k) {
        return cb(obj[k], k);
      });
  }

  /**
   * Flattens an array of arrays by one level
   * @param {array[]} arrs
   * @return {array}
   */
  function flatten(arrs) {
    return arrs.reduce(function (flattened, arr) {
      return flattened.concat(arr);
    }, []);
  }

  /**
   * @param {array} arr
   * @param {function} cb
   * @return {array}
   */
  function flatMap(arr, cb) {
    return flatten(arr.map(cb));
  }

  /**
   * @param {string} prop
   * @return {function}
   */
  function property(prop) {
    return function (o) {
      return o[prop];
    };
  }

})();

(function () {
  'use strict';

  angular
    .module('trcBio', [
      'ngResource'
    ]);

})();

/**
 * Data model representing a person returned by the TRC REST API
 * @typedef Person
 * @type {object}
 * @property {string}           id       - a unique identifier for this person; assigned upon creation
 * @property {EntryMeta}        meta     - metadata about the record and its history in the system
 * @property {PersonName}       name     - primary name used to identify this person
 * @property {PersonName[]}     altNames - other names by which this person is known
 * @property {?HistoricalEvent} birth    - the date and location this person was born; may be null if unknown
 * @property {?HistoricalEvent} death    - the date and location this person was born; may be null if unknown or still alive
 * @property {string}           summary  - an HTML summary of the person and his or her significance
 */

/**
 * Simplified, lightweight representation of a person for use in search results
 * @typedef Person
 * @type {object}
 * @property {string}     id             - a unique identifier for the person
 * @property {EntryMeta}  meta           - metadata about the record and its history in the system
 * @property {PersonName} name           - structured name representation
 * @property {string}     label          - formatted name to display
 * @property {string}     summaryExcerpt - a leading excerpt from the full summary
 */

/**
 * Metadata about a TRC entry
 * @typedef EntryMeta
 * @type {object}
 * @property {string}                      slug         - a short, unique identifier intended to be more human-readable than the id
 * @property {integer}                     version      - a monotonically increasing version number incremented on each change
 * @property {object.<string, SimpleLink>} links        - links to related resources; keyed by link role
 * @property {string}                      lastModified - ISO 8601 timestamp of the last modification date
 */

/**
 * Container for linking to related entities
 * @typedef SimpleLink
 * @type {object}
 * @property {string} uri  - a URI representing the linked item
 * @property {string} type - MIME type of the related linked item
 */

/**
 * Structured form of a person's name
 * @typedef PersonName
 * @type {object}
 * @property {string} role        - clarifies the use of this name
 * @property {string} label       - a simple representation of this name for display purposes
 * @property {?string} title      - title of address
 * @property {?string} givenName  - given or first name
 * @property {?string} middleName - middle name
 * @property {?string} familyName - family or last name
 * @property {?string} suffix     - a suffix (e.g. 'Jr' or 'III')
 */

/**
 * Event with a date/time, location, and descriptive information
 * @typedef HistoricalEvent
 * @type {object}
 * @property {string}          id          - a unique identifier for this event
 * @property {string}          title       - a brief title of the event for display purposes
 * @property {string}          description - a detailed summary of the event
 * @property {string}          location    - the location in which the event took place
 * @property {DateDescription} date        - the date the event took place
 */

/**
 * Container for encapsulating human-readable and machine-readable forms of a date.
 * @typedef DateDescription
 * @type {object}
 * @property {string} calendar    - machine-readable ISO 8601 timestamp
 * @property {string} description - human-readable description of this date
 */

/**
 * Container for search results
 * @typedef PersonSearchResultSet
 * @type {object}
 * @property {SimplePerson[]} items  - search results
 * @property {string}         qs     - query string that gives the current set of search results
 * @property {?string}        qsNext - query string that gives next set of search results or null if this is the last set
 * @property {?string}        qsPrev - query string that gives preceding set of search results or null if this is the first set
 */

(function () {
  'use strict';

  angular
    .module('trcBio')
    .provider('peopleRepo', peopleRepoProvider);


  function peopleRepoProvider() {
    var provider = {};
    provider.url = '/api/people';
    provider.$get = peopleRepoFactory;
    return provider;

    /** @ngInject */
    function peopleRepoFactory($q, $resource) {
      var restResource = $resource(provider.url + '/:id', null, {
        'update': { method: 'PUT' }
      });

      var repo = {};
      repo.create = createPerson;
      repo.createName = createName;
      repo.createEvent = createEvent;
      repo.get = findById;
      repo.search = search;
      repo.save = savePerson;
      repo.delete = deletePerson;
      return repo;

      /**
       * Creates a new empty person instance.
       *
       * @return {Person}
       */
      function createPerson() {
        var person = new restResource();
        return adaptPerson(person);
      }

      /**
       * Creates a new empty entry metadata instance
       *
       * @return {EntryMeta}
       */
      function createMeta() {
        var meta = {};
        return adaptMeta(meta);
      }

      /**
       * Creates a new empty name instance
       *
       * @return {PersonName}
       */
      function createName() {
        var name = {};
        return adaptName(name);
      }

      /**
       * Creates a new historical event instance
       *
       * @return {HistoricalEvent}
       */
      function createEvent() {
        var evt = {};
        return adaptEvent(evt);
      }

      /**
       * Creates a date/description instance
       *
       * @return {DateDescription}
       */
      function createDateDescription() {
        var dateDescription = {};
        return dateDescription;
      }

      /**
       * Retrieves a person object by identifier.
       *
       * A proxy object will be returned immediately, and its properties will be populated after the
       * request succeeds. If fine-grained asynchronous handling, etc. is required, the request's
       * promise is available on Person.$promise.
       *
       * @param  {string} id
       * @return {Person}
       */
      function findById(id) {
        return restResource.get({ id: id });
      }

      /**
       * Retrieves a serch result set of people who match the given query.
       *
       * @param {string} query
       * @return {PersonSearchResultSet}
       */
      function search(query) {
        return restResource.get({ q: query });
      }

      /**
       * Saves a person model to the REST API.
       *
       * If no ID is provided on the person model, the API will create save the model as a new
       * person. Otherwise, the API will attempt to update the existing person model with that ID.
       *
       * @param {Person} person
       * @return {Promise.<Person>} - resolves to the saved person on success
       */
      function savePerson(person) {
        var response;
        if (person.id) {
          response = restResource.update({ id: person.id }, person);
        } else {
          response = restResource.save(null, person);
        }

        return response.$promise.then(function (personId) {
          person.id = personId.id;
          return person;
        });
      }

      /**
       * Deletes a person on the server via the REST API.
       *
       * @param {Person|string} idOrPerson
       * @return {Promise} - resolves on success
       */
      function deletePerson(id) {
        var response = restResource.delete({ id: id });
        return response.$promise;
      }

      /**
       * Populates a person with aggregate fields
       *
       * @param  {Person} person
       * @return {Person}
       */
      function adaptPerson(person) {
        if (!person.meta) {
          person.meta = createMeta();
        } else {
          adaptMeta(person.meta);
        }

        if (!person.name) {
          person.name = createName();
        } else {
          adaptName(person.name);
        }

        if (!person.altNames) {
          person.altNames = [];
        } else {
          person.altNames.forEach(adaptName);
        }

        if (!person.birth) {
          person.birth = createEvent();
        } else {
          adaptEvent(person.birth);
        }

        if (!person.death) {
          person.death = createEvent();
        } else {
          adaptEvent(person.death);
        }

        return person
      }

      /**
       * Ensure that entry metadata aggregate fields exist and are formatted correctly.
       *
       * @param  {EntryMeta} meta
       * @return {EntryMeta}
       */
      function adaptMeta(meta) {
        if (!meta.links) {
          meta.links = {};
        }

        return meta;
      }

      /**
       * Ensure that name aggregate fields exist and are formatted correctly
       * @param  {PersonName} name
       * @return {PersonName}
       */
      function adaptName(name) {
        // no aggregate fields yet...
        return name;
      }

      /**
       * Ensure that event aggregate fields exist and are formatted correctly.
       *
       * @param  {HistoricalEvent} evt
       * @return {HistoricalEvent}
       */
      function adaptEvent(evt) {
        if (!evt.date) {
          evt.date = createDateDescription();
        }

        return evt;
      }
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('trcBio')
    .directive('trcPersonEditor', trcPersonEditorDirective);

  /** @ngInject */
  function trcPersonEditorDirective() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/trc-bio/person-editor.html',
      scope: {
        person: '=ngModel'
      },
      controller: PersonEditorController,
      controllerAs: 'personEditor'
    };

    return directive;

    /** @ngInject */
    function PersonEditorController($scope, peopleRepo) {
      var vm = this;

      vm.addName = addName;
      vm.removeName = removeName;
      vm.addEvent = addEvent;
      vm.removeEvent = removeEvent;

      /**
       * Adds a name to the current person model.
       */
      function addName() {
        var newName = peopleRepo.createName();
        $scope.person.altNames.push(newName);
      }

      /**
       * Removes the name at the specified index from the current person model.
       *
       * @param {integer} ix
       */
      function removeName(ix) {
        $scope.person.altNames.splice(ix, 1);
      }

      /**
       * Adds an event to the current person model.
       *
       * @param {string} key - 'birth' or 'death'
       */
      function addEvent(key) {
        var newEvent = peopleRepo.createEvent();
        $scope.person[key] = newEvent;
      }

      /**
       * Removes the event from the current person model.
       *
       * @param {string} key - 'birth' or 'death'
       */
      function removeEvent(key) {
        $scope.person[key] = null;
      }
    }
  }

})();

