(function () {
   'use strict';

   angular
      .module('slideToggle')
      .directive('slideToggle', slideToggle);

   /** @ngInject */
   function slideToggle() {
      var directive = {
         restrict: 'A',
         scope: {
            active: '=slideToggle'
         },
         link: linkFunc
      };

      return directive;

      function linkFunc(scope, el, attr) {
         var duration = parseInt(attr.slideToggleDuration) || 200;
         scope.$watch('active', function (active) {
            if (active) {
               el.stop().slideDown(duration);
            } else {
               el.stop().slideUp(duration);
            }
         })
      }
   }

})();

(function () {
  'use strict';

  angular
    .module('sdaAdminWeb')
    .controller('ShowPersonController', ShowPersonController);

  /** @ngInject */
  function ShowPersonController(peopleRepo, $stateParams, $mdDialog, $mdToast) {
    var vm = this;

    vm.loading = true;
    vm.person = null;

    vm.editBioInfo = editBioInfo;
    vm.editEvent = editEvent;
    vm.editSummary = editSummary;

    activate();

    function activate() {
      vm.person = peopleRepo.get($stateParams.id);
    }

    function editBioInfo($event) {
      var dialog = {
        targetEvent: $event,
        templateUrl: 'app/person/person-edit-dialog.html',
        locals: {
          // create a copy for manipulation
          person: angular.copy(vm.person)
        },
        controller: 'PersonEditDialogController',
        controllerAs: 'vm'
      };

      var dialogPromise = $mdDialog.show(dialog);

      dialogPromise.then(function (updatedPerson) {
        // copy updates back to original only after dialog is positively dismissed (i.e. not canceled)
        angular.extend(vm.person, updatedPerson);

        peopleRepo.save(vm.person).then(showSavedToast);
      });
    }

    function editEvent(field, $event) {
      var dialog = {
        targetEvent: $event,
        templateUrl: 'app/person/event-edit-dialog.html',
        locals: {
          // create a copy for manipulation
          event: angular.copy(vm.person[field])
        },
        controller: 'EventEditDialogController',
        controllerAs: 'vm'
      };

      var dialogPromise = $mdDialog.show(dialog);

      dialogPromise.then(function (updatedEvent) {
        // copy updates back to original only after dialog is positively dismissed (i.e. not canceled)
        angular.extend(vm.person[field], updatedEvent);

        peopleRepo.save(vm.person).then(showSavedToast);
      });
    }

    function editSummary($event) {
      var dialog = {
        targetEvent: $event,
        templateUrl: 'app/components/summary-edit-dialog/summary-edit-dialog.html',
        locals: {
          // create a copy for manipulation (even if it is just a string)
          summary: angular.copy(vm.person.summary)
        },
        controller: 'SummaryEditDialogController',
        controllerAs: 'vm'
      };

      var dialogPromise = $mdDialog.show(dialog);

      dialogPromise.then(function (updatedSummary) {
        // copy updates back to original only after dialog is positively dismissed (i.e. not canceled)
        vm.person.summary = updatedSummary;

        peopleRepo.save(vm.person).then(showSavedToast);
      });
    }

    function showSavedToast() {
      var toast = $mdToast.simple()
        .textContent('Saved')
        .position('bottom right');

      return $mdToast.show(toast);
    }
  }

})();

(function () {
  'use strict';

  angular
    .module('sdaAdminWeb')
    .controller('PersonEditDialogController', PersonEditDialogController);

  /** @ngInject */
  function PersonEditDialogController(peopleRepo, $mdDialog, person) {
    var vm = this;

    vm.person = person;
    vm.nameRoles = [
        // { value: 'canonical', label: 'Canonical Name' },
        { value: 'pseudonym', label: 'Pseudonym' },
        { value: 'maiden', label: 'Maiden Name' },
        { value: 'designation', label: 'Honorary Designation' },
        { value: 'nickname', label: 'Nickname' },
        { value: 'phonetic', label: 'Phonetic Name' }
    ]

    vm.close = close;
    vm.cancel = cancel;
    vm.addName = addName;
    vm.deleteName = deleteName;

    function close() {
      $mdDialog.hide(vm.person);
    }

    function cancel() {
      $mdDialog.cancel();
    }

    function addName() {
      var name = peopleRepo.createName();
      vm.person.altNames.push(name);
    }

    function deleteName(name) {
      var ix = vm.person.altNames.indexOf(name);
      if (ix >= 0) {
        vm.person.altNames.splice(ix, 1);
      }
    }
  }

})();

